import React, { useState, useEffect } from "react";
import { Note } from "./components/Note";
import noteService from "./services/notes";
import "./App.css";

export const App = () => {
  const [notes, setNotes] = useState([]);
  const [newNote, setNewNote] = useState("");
  const [showAll, setShowAll] = useState(true);

  const label = showAll ? "Collapse Unimportant" : "Expand All";

  const hook = () => {
    noteService.getAll().then((initialNotes) => {
      setNotes(initialNotes);
    });
  };
  useEffect(hook, []);

  const postHook = (event) => {
    event.preventDefault();
    if (newNote !== "") {
      const noteObject = {
        content: newNote,
        date: new Date().toISOString(),
        important: Math.random() > 0.5,
	  };

      noteService.create(noteObject).then((returnedNotes) => {
		setNotes(notes.concat(returnedNotes));
		
        setNewNote("");
      });
    }
  };

  const handleNoteChange = (event) => {
    setNewNote(event.target.value);
  };

  const toggleImportance = (id) => {
    const note = notes.find((n) => n.id === id);
    const changedNote = { ...note, important: !note.important };

    noteService.update(id, changedNote).then((returnedNotes) => {
      setNotes(notes.map((note) => (note.id !== id ? note : returnedNotes)));
    });
  };

  const handleShowAllClick = () => {
    if (showAll) {
      const newNotes = [];
      notes.map((note) => {
        if (note.important) {
          newNotes.push(note);
        }
      });
      setNotes(newNotes);
    } else {
      noteService.getAll().then((initialNotes) => {
        setNotes(initialNotes);
      });
    }
    setShowAll(!showAll);
  };

  return (
    <div>
      <button onClick={handleShowAllClick}>{label}</button>
      <ul>
        {notes.map((note) => (
          <Note
            key={note.id}
            note={note}
            toggleImportance={() => toggleImportance(note.id)}
            deleteNote={() => {
              noteService.deleteNote(note.id, setNotes);
            }}
          />
        ))}
      </ul>
      <form onSubmit={postHook}>
        <input value={newNote} onChange={handleNoteChange}></input>
        <button type="submit">Add Note</button>
      </form>
    </div>
  );
};

export default App;
